body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #e2e0d2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(27, 17, 3);
}

.App-link {
  color: #062129;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


h1, h2, h3 {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    margin: 10px;
}

p {
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    margin: 5px;
}

/* Colors to denote success and failure */
.Success {
    color: forestgreen
}

.Failure {
    color: red
}

div.Question {
    margin: 4px;
    background: #d4d0c7;
    padding: 10px;
}

div.Answer {
    margin: 4px;
    background: #d4d0c7;
    padding: 10px;
}

button {
    width: 200px;
    padding: 10px;
    margin: 5px;
}
button.Choice {
    background: #8b8885;
    color: rgb(27, 17, 3);
}
button.Selected { /* Fix the CSS later */
    background: white;
    color: rgb(27, 17, 3);
}

input {
    position: relative;
    margin: 10px;
    padding: 10px;
    width: 300px;
    background: #8b8885;

}

div .EssayDisplay {
    margin: 20px;
    padding: 10px;
    font-size: 75%;
    color: #666666;
}

textarea {
    margin: 10px;
    padding: 10px;
    width: 90%;
    background: #d4d0c7;
    color: rgb(27, 17, 3);
    font-size: 14px
}


ScoreReport {
    display: flex;
}
.hidden {
    display: none;
}
